import React from "react"
import { graphql, Link } from "gatsby"
import Author from "../components/Author"

import Layout from "../components/Layout";

const WpArchive = ({ data }) => {

  const wpPostArr = [];
  data = data.allWpPost.nodes;

  for (let i = 0; i < data.length; i++) {
    const { title, content, id, author, uri } = data[i];
    wpPostArr.push({ title, content, id, author, uri });
  }

  return ( 
    <Layout>
      <div>
          {wpPostArr.map((post) => { return(
              <div className={`post-${post.id}`} key={post.id}>
                  <h1><Link to={post.uri}>{post.title}</Link></h1>
                  <div dangerouslySetInnerHTML={{ __html: post.content }} />
                  <Author author={post.author.node} />
              </div>
          )})}
      </div>
    </Layout>
  )
}

export const query = graphql`
  query {
    allWpPost {
        nodes {
            id
            title
            content
            uri
            author {
                node {
                ...Archive
                }
            }
        }
    }
  }
`

export default WpArchive;
